.campus{
    margin: 80px auto;
    width: 90%;
    text-align:center ;
}
.gallary{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 40px;

}
.gallary img{
    width:23% ;
    border-radius: 10px;
}

/* Media Query */
@media(max-width:800px){
    .gallary{
        flex-wrap: wrap;

    }
    .gallary img{
        width: 48%;
        margin-bottom: 25px;
    }
}